.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.selectedStationary{
  position: absolute;
  top:0%;
  left:0%;
  z-index:1000;
  width: 0px;
  height: 0px;
}

.selectedBall {
  width: 0px;
  height: 0px;
  border-radius: 50%;
  font-size:15rem;
  background-color: #f30;
  -webkit-animation-name: selectBallAnimation; /* Safari 4.0 - 8.0 */
  -webkit-animation-duration: 3s; /* Safari 4.0 - 8.0 */
  animation-name: selectBallAnimation;
  animation-duration: 3s;
  position: absolute;
  top:5%;
  left:5%;
  z-index:1000;
}

.selectedBall span, .selectedStationary span {
  display: block;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  
  color: white;
}

/* Safari 4.0 - 8.0 */
@-webkit-keyframes selectBallAnimation {
  0%   {width:90%;height:90%; transform: scale(0);}
  20%  {width:90%;height:90%; transform: scale(1);}
  80%  {width:90%;height:90%; transform: scale(1);}
  100% {width:90%;height:90%; transform: scale(0);}
}

/* Standard syntax */
@keyframes selectBallAnimation {
  0%   {width:90%;height:90%; transform: scale(0);}
  25%  {width:90%;height:90%; transform: scale(1);}
  50%  {width:90%;height:90%; transform: scale(1);}
  100% {width:90%;height:90%; transform: scale(0);}
}

.circle{
  
      background: #090;
      border-radius: 50%;
}
.circle span {
  display: block;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  
  color: white;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
